<template>
  <PageWrapper
    :detail="true"
    dense
    contentFullHeight
    fixedHeight
    contentClass="fix-detail-page"
    style="position: relative"
  >
    <div class="load">
      <a-spin :spinning="formState.spinning" tip="Loading...">
        <div class="process-box">
          <!-- 操作按钮-->
          <div class="header-box">
            <div class="header-left">工作安排设置</div>
            <div v-if="!disabledInput" class="header-right">
              <a-button style="margin-right: 10px" @click="handleCancel">{{
                t('common.cancelText')
              }}</a-button>
              <a-button type="primary" @click="handleSave" :loading="isLoading">
                {{ t('common.saveText') }}</a-button
              >
            </div>
          </div>
          <!-- 表单内容 -->
          <div class="content">
            <a-form ref="formRef" :model="formState" labelAlign="right" autocomplete="off">
              <a-card title="基本信息" :bordered="false">
                <a-row :gutter="32">
                  <a-col v-show="true" :span="24">
                    <a-form-item
                      name="content"
                      label="内容"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请输入内容' }]"
                    >
                      <a-input
                        v-model:value="formState.content"
                        :maxlength="100"
                        :disabled="disabledInput"
                      />
                    </a-form-item>
                  </a-col>
                </a-row>
                <a-row :gutter="32">
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="type"
                      label="类型"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请选择类型' }]"
                    >
                      <DictSelectBox
                        v-model:value="formState.type"
                        :type="`work_schedule_type`"
                        :disabled="disabledInput"
                        @change="handleTypeChange"
                      />
                    </a-form-item>
                  </a-col>
                  <a-col v-if="formState.type === 'data_fill'" v-show="true" :span="12">
                    <a-form-item
                      name="menuName"
                      label="工作菜单"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请选择工作菜单' }]"
                    >
                      <SearchBox
                        v-model:value="formState.menuName"
                        :api="getPageUrl"
                        :columns="searchBoxMenuColumns"
                        vfield="menuName"
                        searchKey="menuName"
                        :pagination="true"
                        :disabled="disabledInput"
                        @change="handleMenuNameChange"
                      />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="32">
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="startDate"
                      label="开始日期"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请输入开始日期' }]"
                    >
                      <a-date-picker
                        v-model:value="formState.startDate"
                        style="width: 100%"
                        value-format="YYYY-MM-DD"
                        :disabled="disabledInput"
                      />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="endDate"
                      label="结束日期"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请输入结束日期' }]"
                    >
                      <a-date-picker
                        v-model:value="formState.endDate"
                        style="width: 100%"
                        value-format="YYYY-MM-DD"
                        :disabled="disabledInput"
                      />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="32">
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="reminderTime"
                      label="建议时间"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请输入建议时间' }]"
                    >
                      <a-time-picker
                        v-model:value="formState.reminderTime"
                        style="width: 100%"
                        format="HH:mm"
                        value-format="HH:mm"
                        :disabled="disabledInput"
                      />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="repeatRule"
                      label="重复类型"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: true, message: '请选择重复类型' }]"
                    >
                      <DictSelectBox
                        v-model:value="formState.repeatRule"
                        :type="`repeatingRules`"
                        :disabled="disabledInput"
                      />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="32">
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="creatorName"
                      label="创建人"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: false }]"
                    >
                      <a-input v-model:value="formState.creatorName" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="createTime"
                      label="创建时间"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: false }]"
                    >
                      <a-input v-model:value="formState.createTime" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="32">
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="updaterName"
                      label="修改人"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: false }]"
                    >
                      <a-input v-model:value="formState.updaterName" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col v-show="true" :span="12">
                    <a-form-item
                      name="updateTime"
                      label="修改时间"
                      :labelCol="{ style: { width: '124px' } }"
                      :rules="[{ required: false }]"
                    >
                      <a-input v-model:value="formState.updateTime" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>
              </a-card>
            </a-form>
            <a-card :bordered="false">
              <BasicTable title="接收范围" @register="registerTable">
                <template v-if="!disabledInput" #toolbar>
                  <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleDetailAdd">
                    {{ t('common.action.create') }}
                  </a-button>
                </template>
                <template v-if="!disabledInput" #bodyCell="{ column, record }">
                  <template v-if="column.key === 'action'">
                    <TableAction :actions="createActions(record)" />
                  </template>
                </template>
              </BasicTable>
            </a-card>
          </div>
        </div>
      </a-spin>
    </div>

    <Dialog v-model:visible="modal.open" title="选择接收范围" @ok="handleSuccess">
      <a-form ref="modalFormRef" :model="formState" name="nameForm" autocomplete="off">
        <a-row>
          <a-col v-show="true" :span="24">
            <CommonTree
              class="fix-tree-layout"
              ref="modalTreeRef"
              :value="treeData"
              :toolbar="false"
              checkable
              :fieldNames="{ key: 'id', title: 'name' }"
            />
          </a-col>
        </a-row>
      </a-form>
    </Dialog>
  </PageWrapper>
</template>

<script lang="ts" setup>
  import { reactive, ref, onMounted, toRaw, UnwrapRef } from 'vue';
  import { useRoute } from 'vue-router';
  import { assign } from 'lodash-es';
  import { IconEnum } from '@/enums/appEnum';
  import { SysMessage } from '@/hooks/web/useMessage';
  import { useI18n } from '@/hooks/web/useI18n';
  import Dialog from '@/components/Framework/Modal/Dialog.vue';
  import { BasicTable, useTable, TableAction, ActionItem } from '@/components/Table';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import SearchBox from '@/components/Framework/Combox/SearchBox.vue';
  import { TreeItem } from '@/components/Tree';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { getPageUrl } from '../menu/menu.data';
  import {
    searchBoxMenuColumns,
    detailColumns,
    saveData,
    getData,
    getReceiveRangeList,
    getPostTree,
  } from './schedule.data';
  import { closeCurrentTab } from '@/utils/route';
  import { MsgManager } from '@/message/MsgManager';
  import { PageWrapper } from '/@/components/Page';

  const { t } = useI18n();
  const { query } = useRoute();
  const isLoading = ref(false);
  const formRef = ref();
  const formState: UnwrapRef<any> = reactive({ spinning: false, status: 1 });
  const detailDataSource = ref<any[]>();
  const disabledInput = ref<boolean>(Boolean(query.isReadonly));
  const modalTreeRef = ref();
  const treeData = ref<TreeItem[]>([]);
  const modal = reactive({
    open: ref<boolean>(false),
  });

  const [registerTable, { getDataSource }] = useTable({
    dataSource: detailDataSource,
    columns: detailColumns,
    bordered: true,
    useSearchForm: false,
    canResize: true,
    pagination: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    actionColumn: {
      width: 100,
      title: t('common.operate'),
      dataIndex: 'action',
      fixed: 'right',
      ifShow: () => {
        return !disabledInput.value;
      },
    },
  });

  function handleDetailAdd() {
    modalTreeRef.value?.cancelCheck();
    modal.open = true;
  }

  const handleDetailDelete = async (record: any) => {
    const data = getDataSource();
    const index = data.findIndex((item) => item.key === record.key);
    data.splice(index, 1);
    record.onEdit?.(false, false);
  };

  function createActions(record: any): ActionItem[] {
    return [
      {
        label: t('common.delText'),
        icon: IconEnum.DELETE,
        danger: true,
        popConfirm: {
          title: '是否确认删除',
          confirm: handleDetailDelete.bind(null, record),
        },
      },
    ];
  }

  const handleCancel = () => {
    closeCurrentTab();
    MsgManager.getInstance().sendMsg('work-schedule', {});
  };

  const handleSave = () => {
    formRef.value
      .validate()
      .then(() => {
        const formData = toRaw(formState);
        if (compareDate(formData.startDate, formData.endDate)) {
          SysMessage.getInstance().error('开始日期必须小于或等于结束日期');
          return;
        }
        const data = getDataSource();
        if (data.length === 0) {
          SysMessage.getInstance().error('请选择接收范围');
          return;
        }
        formData.receiveRangeList = data;
        doSave(formData);
      })
      .catch((error) => {
        console.log('onSubmit=》error', error);
      });
  };

  const compareDate = (d1, d2) => {
    const date1 = new Date(Date.parse(d1));
    const date2 = new Date(Date.parse(d2));
    return date1 > date2;
  };

  const doSave = async (formData) => {
    formState.spinning = true;
    try {
      await saveData(formData).then((res) => {
        formState.id = res.result;
        SysMessage.getInstance().success(t('common.saveSuccessText'));
        MsgManager.getInstance().sendMsg('home-page-work-schedule', {});
      });
      setTimeout(function () {
        handleCancel();
      }, 200);
    } finally {
      formState.spinning = false;
    }
  };

  const initFormState = async () => {
    assign(formState, query);
    if (query.id) {
      const data = await getData(query.id as string);
      assign(formState, data);
    }
  };

  const handleTypeChange = (value) => {
    if (value !== 'data_fill') {
      formState.menuId = '';
      formState.menuName = '';
      formState.workMenuId = '';
    }
  };

  const handleMenuNameChange = (value, selectRow) => {
    formState.menuId = selectRow?.record.menuId;
    formState.workMenuId = selectRow?.record.key;
  };

  const handleSuccess = () => {
    const checkedNodes = modalTreeRef.value.getCheckedKeys();
    if (checkedNodes.length === 0) {
      return;
    }
    const station = {};
    let detailList = [] as any;
    for (let index = 0; index < checkedNodes.length; index++) {
      const element = checkedNodes[index];
      if (element.nodeKindId !== 'pos') {
        continue;
      }
      let post = station[element.powerStationId];
      if (post) {
        post.id.push(element.id);
        post.name.push(element.name);
        continue;
      }
      station[element.powerStationId] = { id: [element.id], name: [element.name] };
      detailList.push({
        stationOrganId: element.organId,
        stationOrganName: element.organName,
        stationId: element.powerStationId,
        stationName: element.powerStationName,
      });
    }
    const dataSource = getDataSource();
    for (let i = 0; i < detailList.length; i++) {
      const item = detailList[i];
      for (let j = 0; j < dataSource.length; j++) {
        if (item.stationId === dataSource[j].stationId) {
          SysMessage.getInstance().error(`电站【${dataSource[j].stationName}】已存在`);
          return;
        }
      }
      for (let stationId in station) {
        if (stationId === item.stationId) {
          item.postId = station[stationId].id.join(',');
          item.postName = station[stationId].name.join(',');
        }
      }
    }
    detailDataSource.value = dataSource.concat(detailList);
    modal.open = false;
  };

  const initTableData = async () => {
    detailDataSource.value = await getReceiveRangeList(formState.id);
  };

  async function queryTreeList() {
    const treeList = await getPostTree({});
    // dig(treeList);
    treeData.value = treeList as unknown[] as TreeItem[];
  }

  // 禁用选择框
  // function dig(data) {
  //   const list = [] as any;
  //   for (let i = 0; i < data.length; i++) {
  //     const item = data[i];
  //     const treeNode = item;
  //     treeNode.disableCheckbox = item.nodeKindId !== 'pos';
  //     if (item.children && item.children.length > 0) {
  //       treeNode.children = dig(item.children);
  //     }
  //     list.push(treeNode);
  //   }
  //   return list;
  // }

  onMounted(async () => {
    await initFormState();
    initTableData();
    queryTreeList();
  });
</script>
<style lang="less" scoped>
  .fix-detail-page {
    :deep(&.vben-page-wrapper-content.detail-content) {
      margin: 0;
    }
  }

  .fix-tree-layout {
    margin: 0 !important;

    :deep(.vben-tree-header .flex.items-center) {
      display: none;
    }
  }

  .process-box {
    :deep(.vben-basic-table-header__toolbar > button.ant-btn:last-child) {
      margin-right: 0;
    }

    & > .content {
      padding-bottom: 10px;
    }
  }
</style>
